<template>
  <v-app-bar app clipped-left clipped-right :color="$store.state.user.schoolData.headerColor==null?'#a6dcef':$store.state.user.schoolData.headerColor" dark>
    <v-toolbar-title class="align-center d-flex">
      <span class="logo-icon">
       <v-list-item-avatar size="40" color="grey darken-3">
          <v-img
            class="elevation-6"
            :src="$store.state.user.schoolData.image==null?'https://s3.amazonaws.com/37assets/svn/765-default-avatar.png':$store.state.user.schoolData.image"
             ></v-img>
        </v-list-item-avatar>
      </span>
      <span class="logo-text ml-2">
          {{$store.state.user.schoolData.name==null?'ANAOKULU':$store.state.user.schoolData.name.toUpperCase()}}
      </span>
    </v-toolbar-title>
    <v-app-bar-nav-icon
      class="d-block d-md-none"
      @click="$vuetify.breakpoint.smAndDown ? setSidebarDrawer(!Sidebar_drawer) : $emit('input', !value)"
    />
    <v-spacer />
    <!---right part -->
  
    <v-menu bottom left transition="scale-transition">
      <template v-slot:activator="{ on }">
        <v-btn dark icon v-on="on">
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>

      <v-list>
        <v-list-item  v-for="(item, i) in $settings.rightHelperButton" :key="i" @click="href">
          <v-list-item-title >
            <div v-if="item.func" @click.prevent="item.func"  :class="item.class">{{ item.title }}</div>
            <div v-else-if="item.to"><router-link  :to="item.to"    :class="item.class">{{ item.title }}</router-link></div>
            <small v-else class="text-danger">{{$err.errHelperButton}}</small>
            </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>
<script>
// Utilities
import { mapState, mapMutations } from "vuex";
export default {
  name: "Header",

  components: {},

  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    userprofile: [
      { title: "My Profile" },
      { title: "My Balance" },
      { title: "Inbox" },
      { title: "Account Setting" },
      { title: "Logout" }
    ],
    href() {
      return undefined;
    }
  }),

  computed: {
    ...mapState(["Sidebar_drawer"]),
 //   ...mapGetters(['headerTitle'])

  },

  methods: {
    ...mapMutations({
      setSidebarDrawer: "SET_SIDEBAR_DRAWER"
    })
  }
};
</script>

<style >
.default-list{
  text-decoration: none !important;
  color: rgb(24, 20, 20)  !important;
}
.primary-list{
  text-decoration: none !important;
}
.text-danger{
  color:  #c70039 !important;
}
</style>