<template>
  <v-navigation-drawer
    v-model="Sidebar_drawer"
    :color="$store.state.user.schoolData.sideBarColor"
    mobile-break-point="960"
    clipped
    :right="$vuetify.rtl"
    mini-variant-width="70"
    :expand-on-hover="expandOnHover"
    app
    id="main-sidebar"
  >
    <v-list dense nav>
      <!---USer Area -->
      <v-list-item v-if="!$settings.sideBarInfoHide" two-line class="px-0">
        <v-list-item-avatar v-if="!$settings.sideBarAvatarHide">
          <img :src="user.image" />
        </v-list-item-avatar>

        <v-list-item-content>
          <v-list-item-title>{{user.name}} {{user.lastName}}</v-list-item-title>
          <v-list-item-subtitle v-if="!$settings.sideBarStatusHide" class="caption">{{user.isManager==1?'Müdür':'Kullanıcı'}}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
      <!---USer Area -->
      <!---Sidebar Items -->
      <v-list-item
        v-for="item in $settings.sideBarItems"
        :key="item.title"
        :to="item.to"
        :active-class="`sideBarButtonActiveColor white--text`"
        link
      >
        <v-list-item-icon>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title>{{ $t(item.title) }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <!---Sidebar Items -->
      
    </v-list>
      <template v-slot:append>
        <div class="pa-2">
          <v-btn block @click="$settings.logout" >{{$t('generate.logout')}}</v-btn>
        </div>
      </template>
  </v-navigation-drawer>
</template>

<script>
import { mapState,mapGetters } from "vuex";
export default {
  name: "Sidebar",
  props: {
    expandOnHover: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
  
  }),
  computed: {
    ...mapState(["SidebarColor", "SidebarBg"]),
    ...mapGetters({
      'user' : 'getActiveUser'
    }),
    Sidebar_drawer: {
      get() {
        return this.$store.state.Sidebar_drawer;
      },
      set(val) {
        this.$store.commit("SET_SIDEBAR_DRAWER", val);
      }
    }
  },
  watch: {
    "$vuetify.breakpoint.smAndDown"(val) {
      this.$emit("update:expandOnHover", !val);
    }
  },

  methods: {}
};
</script>
<style lang="scss">
#main-sidebar{
  box-shadow:1px 0 20px rgba(0,0,0,.08);
  -webkit-box-shadow:1px 0 20px rgba(0,0,0,.08);
  .v-navigation-drawer__border{
    display: none;
  }
  .v-list{
    padding: 8px 15px;
  }
  .v-list-item{
      &__icon--text,
      &__icon:first-child{
        justify-content: center;
        text-align: center;
        width: 20px;
        
      }
      
  }    
}
</style>